body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  background: black;
  color: white;
  font-weight: bold;
  font-size: 20px;
  padding: 6px;
}

.scoreContainer {
  display: inline-block;
  margin-left: 4%;
}

.timeContainer {
  float: right;
  margin-right: 4%;
}

.imageContainer {
  width: 100%;
  height: 480px;
}

.challengeImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.answerContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.answerButton {
  flex-grow: 1;
  width: 50%;
  box-sizing: border-box;
  text-align: center;
  display: inline-block;
  background: black;
  border: 1px solid rgb(71, 71, 71);
  padding: 8px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
